@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700&display=swap");
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: "Roboto", sans-serif;
  color: rgb(18, 18, 18);
}

body {
  background-color: rgb(255, 255, 255);
}
body div.overlay {
  position: fixed;
  display: none;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(18, 18, 18, 0.5);
  z-index: 2;
  align-items: center;
  justify-content: center;
}
body div.overlay div.popup {
  position: absolute;
  top: 375px;
  left: auto;
  width: 920px;
  height: 210px;
  background-color: rgb(255, 255, 255);
  z-index: 2;
}
body div.overlay div.popup button#popup-close-button {
  border: none;
  width: 24px;
  height: 24px;
  background-color: rgb(255, 255, 255);
  font-size: 24px;
  font-weight: 700;
  position: relative;
  left: 850px;
  top: 40px;
}
body div.overlay div.popup button#popup-close-button:hover {
  cursor: pointer;
}
body div.overlay div.popup header {
  font-size: 42px;
  font-weight: 700;
  width: 380px;
  height: 50px;
  margin-top: 56px;
  margin-left: 95px;
  position: relative;
  bottom: 22px;
}
body div.overlay div.popup p {
  font-size: 16px;
  font-weight: 300;
  line-height: 24px;
  margin-top: 24px;
  margin-left: 95px;
  width: 380px;
  position: relative;
  bottom: 22px;
}
body div.overlay div.popup p span#counter {
  font-weight: 700;
}
body div.overlay div.popup button#reset-counter-button {
  font-size: 16px;
  font-weight: 700;
  background-color: rgb(228, 193, 174);
  border: none;
  border-radius: 32px;
  color: #ffffff;
  width: 128px;
  height: 48px;
  position: relative;
  left: 500px;
  bottom: 58px;
  visibility: hidden;
}
body div.overlay div.popup button#reset-counter-button:hover {
  cursor: pointer;
  background-color: #ffffff;
  border: solid;
  border-color: rgb(228, 193, 174);
  border-radius: 32px;
  border-width: 1px;
  color: rgb(228, 193, 174);
}
body main {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
body main section {
  display: flex;
  position: relative;
  top: 96px;
  width: 1440px;
  margin-bottom: 50px;
}
body main section img {
  width: 505px;
  height: 330px;
  margin-left: 230px;
}
body main section article {
  width: 380px;
  margin-left: 95px;
}
body main section article header {
  font-size: 42px;
  font-weight: 700;
  height: 50px;
  margin-top: 40px;
}
body main section article p {
  font-size: 16px;
  font-weight: 300;
  line-height: 24px;
  margin-top: 24px;
  margin-bottom: 32px;
}
body main section article button {
  font-size: 16px;
  font-weight: 700;
  background-color: rgb(228, 193, 174);
  border: none;
  border-radius: 32px;
  color: #ffffff;
  width: 128px;
  height: 48px;
}
body main section article button:hover {
  cursor: pointer;
  background-color: #ffffff;
  border: solid;
  border-color: rgb(228, 193, 174);
  border-radius: 32px;
  border-width: 1px;
  color: rgb(228, 193, 174);
}

@media only screen and (min-width: 1024px) and (max-width: 1439px) {
  body main section {
    align-items: center;
    justify-content: center;
    flex-direction: row;
    width: 90vw;
    margin-left: 10vw;
  }
  body main section img {
    width: 450px;
    margin: auto;
    height: auto;
  }
  body main section article {
    margin-left: 5vw;
    margin-right: 10vw;
  }
}
@media only screen and (min-width: 768px) and (max-width: 1023px) {
  body div.overlay div.popup {
    top: auto;
    width: 70vw;
    height: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  body div.overlay div.popup button#popup-close-button {
    left: 30vw;
  }
  body div.overlay div.popup header {
    font-size: 36px;
    width: 60vw;
    margin-left: 40px;
    margin-right: 30px;
  }
  body div.overlay div.popup p {
    font-size: 16px;
    width: 60vw;
    margin-left: 40px;
    margin-right: 30px;
  }
  body div.overlay div.popup button#reset-counter-button {
    bottom: 60px;
    left: 160px;
  }
  body main section {
    align-items: center;
    justify-content: center;
    flex-direction: row;
    width: 90vw;
    margin-left: 10vw;
  }
  body main section img {
    width: 320px;
    margin: auto;
    height: auto;
  }
  body main section article {
    margin-left: 5vw;
    margin-right: 10vw;
  }
  body main section article header {
    font-size: 36px;
    height: auto;
  }
}
@media only screen and (min-width: 600px) and (max-width: 767px) {
  body div.overlay div.popup {
    top: auto;
    width: 70vw;
    height: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  body div.overlay div.popup button#popup-close-button {
    left: 30vw;
  }
  body div.overlay div.popup header {
    font-size: 28px;
    width: 60vw;
    margin-left: 40px;
    margin-right: 30px;
  }
  body div.overlay div.popup p {
    font-size: 14px;
    width: 60vw;
    margin-left: 40px;
    margin-right: 30px;
  }
  body div.overlay div.popup button#reset-counter-button {
    bottom: 0px;
    left: 0px;
    margin-bottom: 20px;
  }
  body main section {
    align-items: center;
    justify-content: center;
    flex-direction: row;
    width: 90vw;
    margin-left: 10vw;
  }
  body main section img {
    width: 230px;
    margin: auto;
    height: auto;
  }
  body main section article {
    margin-left: 5vw;
    margin-right: 10vw;
  }
  body main section article header {
    font-size: 28px;
    height: auto;
  }
  body main section article p {
    font-size: 14px;
  }
}
@media only screen and (max-width: 599px) {
  body div.overlay div.popup {
    top: auto;
    width: 80vw;
    height: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  body div.overlay div.popup button#popup-close-button {
    left: 30vw;
  }
  body div.overlay div.popup header {
    font-size: 24px;
    width: 70vw;
    margin-left: 40px;
    margin-right: 30px;
  }
  body div.overlay div.popup p {
    font-size: 14px;
    width: 70vw;
    margin-left: 40px;
    margin-right: 30px;
  }
  body div.overlay div.popup button#reset-counter-button {
    bottom: 0px;
    left: 0px;
    margin-bottom: 20px;
  }
  body main section {
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100vw;
  }
  body main section img {
    width: 60vw;
    margin: auto;
    height: auto;
  }
  body main section article {
    width: 80vw;
    margin: 0px 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  body main section article header {
    font-size: 24px;
    margin-top: 20px;
    height: auto;
    text-align: center;
  }
  body main section article p {
    font-size: 14px;
    margin-top: 10px;
    margin-bottom: 20px;
  }
}